import React, { Component } from "react";
import "./App.css";

class App extends Component {
  state = {
    date: "",
    price: ""
  };

  handleClickBTC = () => {
    const API = `https://financialmodelingprep.com/api/v3/cryptocurrency/btc`;
    fetch(API)
      .then(response => {
        if (response.ok) {
          return response;
        }
        throw Error("Brak połączenia z API");
      })
      .then(response => response.json())
      .then(data => {
        const time = new Date().toLocaleString();
        this.setState({
          date: time,
          price: data.price
        });
      });
  };
  handleClickETH = () => {
    const API = `https://financialmodelingprep.com/api/v3/cryptocurrency/eth`;
    fetch(API)
      .then(response => {
        if (response.ok) {
          return response;
        }
        throw Error("Brak połączenia z API");
      })
      .then(response => response.json())
      .then(data => {
        const time = new Date().toLocaleString();
        this.setState({
          date: time,
          price: data.price
        });
      });
  };
  handleClickLTC = () => {
    const API = `https://financialmodelingprep.com/api/v3/cryptocurrency/ltc`;
    fetch(API)
      .then(response => {
        if (response.ok) {
          return response;
        }
        throw Error("Brak połączenia z API");
      })
      .then(response => response.json())
      .then(data => {
        const time = new Date().toLocaleString();
        this.setState({
          date: time,
          price: data.price
        });
      });
  };
  handleClickLSK = () => {
    const API = `https://financialmodelingprep.com/api/v3/cryptocurrency/lsk`;
    fetch(API)
      .then(response => {
        if (response.ok) {
          return response;
        }
        throw Error("Brak połączenia z API");
      })
      .then(response => response.json())
      .then(data => {
        const time = new Date().toLocaleString();
        this.setState({
          date: time,
          price: data.price
        });
      });
  };
  handleClickXRP = () => {
    const API = `https://financialmodelingprep.com/api/v3/cryptocurrency/xrp`;
    fetch(API)
      .then(response => {
        if (response.ok) {
          return response;
        }
        throw Error("Brak połączenia z API");
      })
      .then(response => response.json())
      .then(data => {
        const time = new Date().toLocaleString();
        this.setState({
          date: time,
          price: data.price
        });
      });
  };

  render() {
    return (
      <div className="App">
        <h1>Kursy Kryptowalut</h1>
        <section>
          <button onClick={this.handleClickBTC}>BITCOIN</button>
          <button onClick={this.handleClickETH}>ETHEREUM</button>
          <button onClick={this.handleClickLTC}>LITECOIN</button>
          <button onClick={this.handleClickLSK}>LISK</button>
          <button onClick={this.handleClickXRP}>RIPPLE</button>
        </section>
        <h3>
          {" "}
          {this.state.date
            ? `Dane dla dnia i godziny: ${this.state.date}`
            : null}
        </h3>
        <h4>
          {this.state.price
            ? `${Math.round(this.state.price * 100) / 100} $`
            : null}{" "}
        </h4>
      </div>
    );
  }
}

export default App;
